/**
CONTACT CSS
 */
#contact-container > div {
  width: 300px;
  margin: 6vh auto 0 auto;
  display: flex;
  flex-direction: row; }

#contact-container a {
  all: unset;
  flex: 1 1 auto;
  cursor: pointer;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.2s; }

#contact-container svg {
  display: block;
  margin: auto; }

#contact-container > div > a:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2); }

/**
HEADER CSS
 */
#header, #dropdown {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  background: white;
  box-shadow: 1px -2px 15px #9d9d9d;
  transition-property: background-color, box-shadow;
  transition-duration: 0.5s, 0.5s; }

#app.dark #header, #app.dark #dropdown {
  background: #57575a;
  box-shadow: 1px -2px 15px #2b2b2b; }

#theme-switcher {
  float: right !important;
  display: block !important; }

#theme-switcher:hover {
  background: transparent !important; }

#header.is-active, #app.dark #header.is-active {
  box-shadow: 1px -2px 15px transparent; }

#dropdown {
  top: 58px;
  z-index: 9; }

#header > div {
  float: left;
  padding: 20px;
  cursor: pointer; }

#header > div:hover {
  background: #f3f3f3; }

#app.dark #header > div:hover {
  background: #454545; }

#header-title {
  display: block !important; }

#menu {
  float: right !important;
  padding: 15px !important; }

#dropdown {
  display: none; }

#dropdown.is-active {
  display: block; }

#dropdown > div {
  width: 100%;
  padding: 20px;
  cursor: pointer; }

#dropdown > div:hover {
  background: #f3f3f3; }

#app.dark #dropdown > div:hover {
  background: #454545; }

@media only screen and (max-width: 700px) {
  #menu {
    display: block !important; }
  #header > div {
    display: none; } }

@media only screen and (min-width: 701px) {
  #menu {
    display: none; }
  #dropdown {
    display: none !important; } }

/*******************************************************************
** HAMBURGER *******************************************************
*******************************************************************/
.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, -webkit-filter;
  transition-property: opacity, filter;
  transition-property: opacity, filter, -webkit-filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible; }

.hamburger:hover {
  opacity: 0.7; }

.hamburger.is-active:hover {
  opacity: 0.7; }

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: #828286; }

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px; }

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 40px;
  height: 4px;
  background-color: #828286;
  border-radius: 4px;
  position: absolute;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  transition-duration: 0.15s;
  transition-timing-function: ease; }

.hamburger-inner::before, .hamburger-inner::after {
  content: "";
  display: block; }

.hamburger-inner::before {
  top: -10px; }

.hamburger-inner::after {
  bottom: -10px; }

/*
   * Slider
   */
.hamburger--slider .hamburger-inner {
  top: 2px; }

.hamburger--slider .hamburger-inner::before {
  top: 10px;
  transition-property: opacity, -webkit-transform;
  transition-property: transform, opacity;
  transition-property: transform, opacity, -webkit-transform;
  transition-timing-function: ease;
  transition-duration: 0.15s; }

.hamburger--slider .hamburger-inner::after {
  top: 20px; }

.hamburger--slider.is-active .hamburger-inner {
  -webkit-transform: translate3d(0, 10px, 0) rotate(45deg);
          transform: translate3d(0, 10px, 0) rotate(45deg); }

.hamburger--slider.is-active .hamburger-inner::before {
  -webkit-transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
          transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0; }

.hamburger--slider.is-active .hamburger-inner::after {
  -webkit-transform: translate3d(0, -20px, 0) rotate(-90deg);
          transform: translate3d(0, -20px, 0) rotate(-90deg); }

/*******************************************************************
** SWITCH **********************************************************
*******************************************************************/
/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 19px; }

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s; }

.slider:before {
  position: absolute;
  content: "";
  height: 13px;
  width: 13px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: .4s; }

input:checked + .slider {
  background-color: #dd7c57; }

input:focus + .slider {
  box-shadow: 0 0 1px #dd7c57; }

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  transform: translateX(16px); }

/* Rounded sliders */
.slider.round {
  border-radius: 34px; }

.slider.round:before {
  border-radius: 50%; }

