@import url('https://fonts.googleapis.com/css?family=Product+Sans');

$text-color: #828286;
$background-color: #f7f7f7;
$shadow-color: #9d9d9d;
$hover-color: #f3f3f3;

$text-color-dark: #f7f7f7;
$background-color-dark: #57575a;
$shadow-color-dark: #2b2b2b;
$hover-color-dark: #454545;

$highlight-color: #dd7c57;

$full-height: 100vh;
$full-width: 100vw;
$duration: 0.5s;

$side-width: 8vw;
$top-height: 6vh;

body {
  margin: 0;
  padding: 0;
  letter-spacing: 1px;
  font-family: 'Product Sans', Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root {
  margin: 0;
  padding: 0;
  height: $full-height;
  width: $full-width;
}

#app {
  color: $text-color;
  background-color: $background-color;

  transition-property: background-color, color, box-shadow;
  transition-duration: $duration, $duration, $duration;
}

#app.dark {
  color: $text-color-dark;
  background-color: $background-color-dark;

}

#app {
  box-sizing: border-box;
  padding: 0 $side-width 0 $side-width;
}

#footer {
  margin-top: $top-height;
  height: 30px;
  text-align: center;
  font-size: 0.9rem;
}

#footer svg {
  margin-right: 6px;
}

.error {
  color: red;
}

.title {
  font-size: 3em;
  font-weight: bold;
  margin-top: 1vw;
}

hr {
  margin-top: -7px;
  border: 0;
  border-top: 1px solid $highlight-color;
}

#header-title {
  color: $highlight-color;
}

/**
CONTACT CSS
 */

#contact-container > div {
  width: 300px;
  margin: $top-height auto 0 auto;
  display: flex;
  flex-direction: row;
}

#contact-container a {
  all: unset;
  flex: 1 1 auto;
  cursor: pointer;

  transition-property: transform;
  transition-duration: 0.2s;
}

#contact-container svg {
  display: block;
  margin: auto;
}

#contact-container > div > a:hover {
  transform: scale(1.2);
}

/**
HEADER CSS
 */

#header, #dropdown {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  background: white;
  box-shadow: 1px -2px 15px $shadow-color;

  transition-property: background-color, box-shadow;
  transition-duration: 0.5s, 0.5s;
}

#app.dark #header, #app.dark #dropdown {
  background: $background-color-dark;
  box-shadow: 1px -2px 15px $shadow-color-dark;
}

#theme-switcher {
  float: right !important;
  display: block !important;
}

#theme-switcher:hover {
  background: transparent !important;
}

#header.is-active, #app.dark #header.is-active {
  box-shadow: 1px -2px 15px transparent;
}

#dropdown {
  top: 58px;
  z-index: 9;
}

#header > div {
  float: left;
  padding: 20px;
  cursor: pointer;
}

#header > div:hover {
  background: $hover-color;
}

#app.dark #header > div:hover {
  background: $hover-color-dark;
}

#header-title {
  display: block !important;
}

#menu {
  float: right !important;
  padding: 15px !important;
}

#dropdown {
  display: none;
}

#dropdown.is-active {
  display: block;
}

#dropdown > div {
  width: 100%;
  padding: 20px;
  cursor: pointer;
}

#dropdown > div:hover {
  background: $hover-color;
}

#app.dark #dropdown > div:hover {
  background: $hover-color-dark;
}

@media only screen and (max-width: 700px) {
  #menu {
    display: block !important;
  }

  #header > div {
    display: none;
  }
}

@media only screen and (min-width: 701px) {
  #menu {
    display: none;
  }

  #dropdown {
    display: none !important;
  }
}

/*******************************************************************
** HAMBURGER *******************************************************
*******************************************************************/

.hamburger {
  padding: 15px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible; }
.hamburger:hover {
  opacity: 0.7; }
.hamburger.is-active:hover {
  opacity: 0.7; }
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: $text-color; }

.hamburger-box {
  width: 40px;
  height: 24px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px; }
.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  width: 40px;
  height: 4px;
  background-color: $text-color;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease; }
.hamburger-inner::before, .hamburger-inner::after {
  content: "";
  display: block; }
.hamburger-inner::before {
  top: -10px; }
.hamburger-inner::after {
  bottom: -10px; }

/*
   * Slider
   */
.hamburger--slider .hamburger-inner {
  top: 2px; }
.hamburger--slider .hamburger-inner::before {
  top: 10px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s; }
.hamburger--slider .hamburger-inner::after {
  top: 20px; }

.hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(0, 10px, 0) rotate(45deg); }
.hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0; }
.hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -20px, 0) rotate(-90deg); }

/*******************************************************************
** SWITCH **********************************************************
*******************************************************************/

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 35px;
  height: 19px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 13px;
  width: 13px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: $highlight-color;
}

input:focus + .slider {
  box-shadow: 0 0 1px $highlight-color;
}

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/**
INFO CSS
 */

#info {
  box-sizing: border-box;
  height: $full-height;
  padding: 22vh 5vw 0 5vw;
}

#info > div {
  box-sizing: border-box;
  height: 50vh;
}

#info-container {
  box-sizing: border-box;
  float: left;
  padding-bottom: 4vh;
}

img#profile {
  float: right;
  border-radius: 50%;
  box-shadow: 1px 1px 20px $shadow-color;

  transition-property: box-shadow;
  transition-duration: $duration;
}

#app.dark img#profile {
  box-shadow: 1px 1px 20px $shadow-color-dark;
}

@media only screen and (max-width: 800px) {
  #info > div {
    padding-top: 0;
  }

  #info-container {
    width: 74vw;
  }

  img#profile {
    width: 36vw;
    height: 36vw;
    margin: 0 19vw 2vw 0;
  }
}


@media only screen and (min-width: 801px) {
  #info > div {
    padding-top: 10vh;
  }

  #info-container {
    width: 40vw;
    padding-top: 5vh;
  }

  img#profile {
    width: 40vh;
    height: 40vh;
  }
}

/**
PROJECT CSS
 */

.project-container {
  box-sizing: border-box;
  padding-top: 3vw;
  width: 84vw;
  min-height: 36vw;
  position: relative;
}

.full-container {
  width: 84vw;
  min-height: 36vw;
}

.project-text {
  box-sizing: border-box;
  float: left;
}

.project-title {
  font-size: 1.7em;
  margin-bottom: 10px;
  font-weight: bold;
}

.project-inner {
  position: relative;
  box-shadow: 1px 1px 30px $shadow-color;
  overflow: hidden;

  border-radius: 20px;

  transition-property: box-shadow;
  transition-duration: $duration;
}

.project-inner > div:first-child {
  height: 100%;
}

.project-links {
  padding: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.project-links svg:hover {
  transform: scale(1.2);
}

.project-links a {
  float: right;
}

.project-links svg {
  transition-property: transform;
  transition-duration: 0.2s;
  filter: drop-shadow(1px 1px 4px #000000);
  color: $text-color-dark;
  margin-left: 20px;
}

#app.dark .project-inner {
  box-shadow: 1px 1px 30px $shadow-color-dark;
}

@media only screen and (max-width: 1150px) {
  .project-inner {
    width: 84vw;
    height: 47.25vw;
  }

  .project-text {
    width: 84vw;
    padding-bottom: 25px;
  }
}


@media only screen and (min-width: 1151px) {
  .project-inner {
    width: 64vw;
    height: 36vw;
  }

  .project-inner.left {
    float: right;
  }

  .project-inner.right {
    float: left;
  }

  .project-text {
    height: 36vw;
    width: 19.8vw;
  }

  .project-text.left {
    padding-left: 25px;
    float: right;
  }

  .project-text.right {
    padding-right: 25px;
    text-align: right;
    float: left;
  }
}

.project-inner img {
  box-shadow: 1px 1px 15px $shadow-color-dark;
}

