/**
VAARDIGHEDEN CSS
 */

#vaardigheden-container > div {
  box-sizing: border-box;
  margin: 1vw;
  float: left;
  background: white;
  box-shadow: 1px 1px 15px $shadow-color;
  padding: 2vw;

  border-radius: 20px;

  transition-property: background-color, box-shadow;
  transition-duration: $duration, $duration;
}

#app.dark #vaardigheden-container > div {
  background: #6d6d70;
  box-shadow: 1px 1px 15px $shadow-color-dark;
}

#vaardigheden-container hr {
  margin: 0;
}

.vaardigheden-title {
  text-align: center;
  font-weight: bold;
  font-size: 1em;
}

@media only screen and (max-width: 850px) {
  #vaardigheden-container > div {
    width: 84vw;
    margin-bottom: 3vw;
    height: auto;
  }
}


@media only screen and (min-width: 851px) {
  #vaardigheden-container > div {
    width: 25.9vw;
    height: 40vh;
  }
}